<template> 
    <div class="root">
        <h4>Get your Quote</h4>
        <p>This is optional paragraph text which can be left blank or turned off to not show if needed via the CMS.</p>
        <form  @submit.prevent>
        <div class="half-fields field">
            <div class="inputWrapper">
            <label for="firstname">First Name</label>
             
            <input
                name="firstname" 
                type="text" 
                placeholder="First Name" 
                v-model="state.name.firstName"
            />
            <span v-if="v$.name.firstName.$error">
                   {{v$.name.firstName.$errors[0].$message}}         
            </span>
        </div>
         <div class="inputWrapper">
            <label for="lastname">Last Name</label>
            
            <input
                name="lastname" 
                type="text" 
                placeholder="Last Name" 
                v-model="state.name.lastName"
            />
            <span v-if="v$.name.lastName.$error">
                   {{v$.name.lastName.$errors[0].$message}}         
            </span>
        </div>
        </div>
            
        <div class="third-fields field">
        <label class="dob-label" for="dob">Date Of Birth</label>
        
       
        <span class="ageSpan" v-if="v$.dob.year.$error">
                    You must be between 50 and 80 years old to apply.
        </span>      
        <div class="inputWrapper day-wrapper">
          
            <input
                name="dob" 
                type="text" 
                placeholder="DD" 
                v-model="state.dob.day"
                
            />
            <span v-if="v$.dob.day.$error">
                   {{v$.dob.day.$errors[0].$message}}         
        </span>
        </div>
        
         <div class="inputWrapper month-wrapper">
            <input
                name="dob" 
                type="text" 
                placeholder="MM" 
                v-model="state.dob.month"
                
            />
            <span v-if="v$.dob.month.$error">
                   {{v$.dob.month.$errors[0].$message}}         
        </span>
        </div>
        
         <div class="inputWrapper year-wrapper">
            <input
                name="dob"
                type="text" 
                placeholder="YYYY" 
                v-model="state.dob.year"
                
            />
             
           
        </div>
        </div> 
        <div class="field">
        <label for="phone">Phone Number</label>
           
        <div class="inputWrapper year-wrapper">
            <input
                name="phone"
                @keydown.space.prevent 
                type="text" 
                placeholder="Phone Number" 
                v-model="state.phoneNum"
                
            />
            <span v-if="v$.phoneNum.$error">
                   {{v$.phoneNum.$errors[0].$message}}         
            </span>
        </div>
        </div>
        <div class="field">     
        <label for="email">Email</label>
        
        <div class="inputWrapper">
            <input name="email" type="text" placeholder="Email" v-model="state.email"/>
            <span v-if="v$.email.$error">
                   {{v$.email.$errors[0].$message}}         
            </span>
        </div>
        </div>
        <div class="field">
        <label for="postcode">Postcode</label>
        <span class="ageSpan" v-if="v$.postCode.$error">
                   e.g., A1A 1A1         
            </span>          
        <div class="inputWrapper">
            <input
                name="postcode"
                @keydown.space.prevent 
                type="text" 
                placeholder="Post Code" 
                v-model="state.postCode"
                
            />
           <span v-if="v$.postCode.$error">
                   {{v$.postCode.$errors[0].$message}}
                            
            </span>
        </div>
        </div>
        <div class="field">  
        <div class="inputWrapper terms">
           <p  class="terms">You can read our terms of use and our privacy policy. When you click the above button, you agree to us using your phone number and email address to contact you about your quote.</p>
        </div>
        </div>
        <button @click="submitForm">Submit</button>
        </form>
    </div>
</template>
<script>
import useValidate from '@vuelidate/core'
import { required, email, maxLength, numeric, between, alphaNum} from '@vuelidate/validators'
import {reactive, computed } from 'vue'
import moment from 'moment'

//const isDate = (value) => moment(value, 'DD/MM/YYYY', true).isValid()
//const isDate = (value, day, month, year) => moment(day+'/'+month+'/'+year, 'DD/MM/YYYY', true).isValid()
//const olderDate = (value) => moment(value, 'DD/MM/YYYY').isBefore(moment().format('DD/MM/YYYY'), 'day');

  export default {
    setup() {
        const state = reactive({
            email: '',
            name: {
                firstName: '',
                lastName: '',
            },
            dob:{
                day:'',
                month:'',
                year:'',
            },
            phoneNum: '',
            postCode: '',
        })
   
        const rules = computed(() => {
            
            return{
                email: { required, email },
                name: {
                    firstName: { required },
                    lastName: { required },
                },
                dob:{
                    day: { required, between: between(1, 31)},
                    month: { required,  between: between(1, 12)},
                    year: { required, between: between(moment().year()-80, moment().year()-50)},   
                    // isDate: helpers.withMessage(
                    //     'Not Valid Date (DD/MM/YYYY)',
                    //     isDate
                    // ),
                    // // olderDate: helpers.withMessage(
                    // //     'Please enter a date older than todays date',
                    // //     olderDate
                    // // ),
                   
                },
                phoneNum: { 
                    required,  
                    minLength: maxLength(10), 
                    numeric
                },
               postCode: { 
                    required,  
                    minLength: maxLength(6), 
                    alphaNum
                },
            }
        })

        const v$ = useValidate(rules, state)

        return{
            state,
            v$
        }
    },
    
    methods:{
        submitForm(){
            this.v$.$validate()
            if(!this.v$.$error){
                alert('Form succesfully submitted.')
            }else{
                console.log(moment().format('DD/MM/YYYY'))
                console.log(this.v$)
                alert('Form Failed Validation')
            }
            
        },
    },

};
</script>
<style lang="scss">
.root{
    background-color: #F9F9F9;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    padding: 24px;
    max-width: 100%;
    float:right;
    p{
        margin:0;
    }
    form{
        width:100%;
       text-align: start;
       
        div.field{
            margin-top:24px;
            display: flex;
            flex-direction: column;
            
            span{
                margin-top:4px;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: #222222;
            }
            span.ageSpan{
                margin-top:0px;
                margin-bottom: 4px;
            }
        }
        button{
            padding: 16px 32px;
            gap: 4px;
            background: #F3B61A;
            width:100%;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius: 100px;
            border:0px solid;
            margin-top: 24px;
            font-family: 'Ubuntu';
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 32px;
            color: #243487;
            cursor: pointer;
        }
        input{
            width:100%;
            padding: 10px 8px;
            gap: 10px;
            background: #FFFFFF;
            border: 1px solid #C4C4C4;
            border-radius: 4px;
        }
        div.inputWrapper{
            display: flex;
            flex-direction: column;
            align-items: flex-start;

        }
        label{
            font-family: 'Ubuntu';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            color: #153758;
            margin-bottom:4px;
        }
        p.terms{
            font-weight: 300;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: #626262;
        }
         div.half-fields.field{
            flex-direction: row;
            align-items: flex-end;
            div.inputWrapper{
            flex:1 0 50%;
            }
            div:first-child {
                padding-right:12px;
            }
            div:last-child {
                 padding-left:12px;
            }
        }
        div.third-fields.field{
            
            align-items: flex-end;
            flex-wrap: wrap;
            flex-direction: row;

            label:first-child {
                width: 100%;
            }
            div.day-wrapper{
                padding-right:5px;
            }
            div.month-wrapper{
                padding:0px 5px;
            }
            div.year-wrapper{
                padding-left:5px;
            }
            div.inputWrapper{
            flex:1 0 33%;
            }
        }
    }
}    

    .root{
    max-width:100%;
    form{
        
    
    }
  }
</style>